<template>
  <div :data="dataObj" border style="width: 100%" v-loading="detaileLoading">
    <!-- Trust Level and Segment -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ ' Trust Level and Segment' }}</span>
      </div>
      <div class="group-card__body">
        <el-form-item class='none-margin-bottom'>
          <el-descriptions
            :column="2"
            direction="vertical"
            :colon="false"
            :label-style="labelStyle"
            :content-style="contentStyle"
          >
            <el-descriptions-item label="Trust Level"
              >{{ dataObj.trustLevel }}:{{ dataObj.trustLevelName }}</el-descriptions-item
            >
            <el-descriptions-item label="Segment">{{ dataObj.selectSegmentName }}</el-descriptions-item>
          </el-descriptions>
        </el-form-item>
      </div>
    </div>
    <div class="segmentation"></div>
    <!-- First Member -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ ' First Member' }}</span>
      </div>
      <div class="group-card__body">
        <el-row>
          <el-col>
            <el-form-item
              label="MSISDN"
              required
              prop="msisdn"
              :rules="[{ validator: checkGroupMsisdnOnBlur, trigger: 'blur' }]"
            >
              <el-row>
                <el-col v-bind="colOptions">
                  <el-input size="small" v-model="dataObj.msisdn" placeholder="Please enter"></el-input>
                </el-col>
              </el-row>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </div>

    <div class="segmentation"></div>
    <!-- Product -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ 'Product' }}</span>
      </div>
      <div class="group-card__body">
        <el-form-item>
          <table-page :columns="selectProductColums" :has-pagination="true" :outSideTableData="dataObj.productInfoList">
            <template slot="effectiveDate" slot-scope="scope">
              <el-form-item
                :prop="`productInfoList.${scope.data.$index}.effectiveDate`"
                :rules="[
                  {
                    required: true,
                    message: 'Please select content',
                    trigger: 'change',
                  },
                  { validator: getEffectiveDateRange(scope.data.row.expireDate), trigger: 'blur' },
                ]"
              >
                <el-date-picker
                  v-model="scope.data.row.effectiveDate"
                  type="date"
                  size="small"
                  :placeholder="$t('common.placeholder.enter')"
                  style="width: 100%"
                  format="dd/MM/yyyy"
                  value-format="dd/MM/yyyy HH:mm:ss"
                  :picker-options="
                    pickerOptionsEffectiveDate(scope.data.row.effectiveDateInit, scope.data.row.expireDateInit)
                  "
                >
                </el-date-picker>
              </el-form-item>
            </template>
            <template slot="expireDate" slot-scope="scope">
              <el-form-item
                :prop="`productInfoList.${scope.data.$index}.expireDate`"
                :rules="[
                  {
                    required: true,
                    message: 'Please select content',
                    trigger: 'change',
                  },
                  { validator: getExpireDateRange(scope.data.row.effectiveDate), trigger: 'blur' },
                ]"
              >
                <el-date-picker
                  v-model="scope.data.row.expireDate"
                  format="dd/MM/yyyy"
                  value-format="dd/MM/yyyy HH:mm:ss"
                  type="date"
                  size="small"
                  :placeholder="$t('common.placeholder.enter')"
                  style="width: 100%"
                  :picker-options="
                    pickerOptionsEffectiveDate(scope.data.row.effectiveDateInit, scope.data.row.expireDateInit)
                  "
                >
                </el-date-picker>
              </el-form-item>
            </template>
            <template slot="chargeProfileId" slot-scope="scope">
              <el-select size="small" v-model="scope.data.row.chargeProfileId" placeholder="Please select">
                <el-option
                  v-for="item in scope.data.row.chargeProfiles"
                  :key="item.chargeProfileId"
                  :label="item.chargeProfileName"
                  :value="item.chargeProfileId"
                >
                </el-option>
              </el-select>
            </template>
          </table-page>
        </el-form-item>
      </div>
    </div>

    <div class="segmentation"></div>
    <!-- Group Member Role -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ 'Group Member Role' }}</span>
      </div>
      <div class="group-card__body">
        <el-form-item>
          <el-table style="width: 100%" :data="dataObj.groupMemberRoleList" empty-text="No records found.">
            <el-table-column fixed label="Group Role Name" prop="name"></el-table-column>
            <el-table-column fixed label="Minimum Number Required" prop="minNumRequired"></el-table-column>
            <el-table-column fixed label="Maximum Number Allowed" prop="maxNumAllowed"></el-table-column>
            <el-table-column fixed label="Assign to First Member by Default" prop="default">
              <template slot-scope="scope">
                {{ scope.row.default ? 'Yes' : 'No' }}
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
      </div>
    </div>

    <div class="segmentation"></div>
    <!-- Profile -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ 'Profile' }}</span>
      </div>
      <div class="group-card__body">
        <el-row class="detail" :gutter="20">
          <el-col v-bind="colOptions">
            <el-form-item class="identity" label="Charge Profile" prop="chargeProfileobj">
              <el-select
                v-model="dataObj.chargeProfileobj.chargeProfileId"
                placeholder="Please select"
                @change="seletedChargeProfile"
                clearable
              >
                <el-option
                  v-for="item in dataObj.selectChargeProfileList"
                  :key="item.chargeProfileId"
                  :label="item.chargeProfileName"
                  :value="item.chargeProfileId"
                >
                </el-option>
              </el-select>
              <div class="identity-btn" @click="chargeProfileUseDefault">{{ 'Use Default' }}</div>
              <!-- <div class="profile-btn" @click="profileUseDefault">{{ 'Use Default' }}</div> -->
            </el-form-item>
          </el-col>
          <el-col v-bind="colOptions" class="identity">
            <el-form-item label="Rule Profile" required prop="ruleProfileobj">
              <el-select
                v-model="dataObj.ruleProfileobj.ruleProfileId"
                placeholder="Please select"
                @change="seletedRuleProfile"
              >
                <el-option
                  v-for="item in dataObj.selectRuleProfileList"
                  :key="item.ruleProfileId"
                  :label="item.ruleProfileName"
                  :value="item.ruleProfileId"
                >
                </el-option>
              </el-select>
              <div class="identity-btn" @click="ruleProfileUseDefault">{{ 'Use Default' }}</div>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </div>

    <div class="segmentation"></div>
    <!-- Account -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ 'Account' }}</span>
      </div>
      <div class="group-card__body">
        <el-form-item>
          <el-table style="width: 100%" :data="dataObj.accountTypeList">
            <el-table-column fixed label="Account Type" prop="accountType.accountTypeName"></el-table-column>
            <el-table-column fixed label="Currency" prop="currency"></el-table-column>
            <el-table-column fixed label="Rule Profile" prop="ruleProfile">
              <template slot-scope="scope">
                <el-select
                  size="small"
                  v-model="scope.row.ruleProfileId"
                  placeholder="Please select"
                  v-if="!scope.row.accountType.limitExclude"
                >
                  <el-option
                    v-for="item in scope.row.ruleProfileBeans"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
                <span v-else> -- </span>
              </template>
            </el-table-column>
            <el-table-column fixed label="Operation">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="text"
                  @click="handleUseDefault(scope)"
                  v-if="!scope.row.accountType.limitExclude"
                  >Use Default</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
      </div>
    </div>

    <!-- 多选弹框 -->
    <product-dialog
      :productVisible.sync="productVisible"
      :addForm="productForm"
      :isCheck="true"
      :isCheckMemory="true"
      :hasPagination="true"
      :childSearch="loadProductParam"
      @submit-call-back="onProductChange"
    ></product-dialog>
  </div>
</template>
<script>
import TablePage from '@/components/tablePage';
import productDialog from '../../../customer/create/components/product-dialog.vue';
import { queryAssignProduct, checkMsisdn } from '../../../../api/api-group';
import dayjs from 'dayjs';
export default {
  name: 'GroupCreateDetails',
  components: { TablePage, productDialog },
  props: {
    dataObj: {
      labels: Object,
      default() {
        return {};
      },
    },
    detaileLoading: {
      type: Boolean,
      default: false,
    },
  },
  provide() {
    return {};
  },
  data() {
    return {
      labelStyle: {
        'font-size': '14px !important',
        color: `var(--descriptions-label-color) !important`,
        'font-weight': '400 !important',
        padding: '0 10px 8px 0 !important',
        'font-family': 'Huawei Sans',
      },
      contentStyle: {
        'font-size': '14px !important',
        color: `var(--descriptions-content-color) !important`,
        'font-weight': '400 !important',
        padding: '0 10px 20px 0 !important',
        'font-family': 'Huawei Sans',
      },
      productVisible: false,
      productForm: {
        title: this.$t('IDC.orgCustomer.addProduct'),
        idKey: 'productId',
        columns: [
          {
            label: this.$t('IDC.orgCustomer.productName'),
            prop: 'productName',
            'sort-method': (a, b) => {
              return a.productName > b.productName ? 1 : -1;
            },
            align: 'left',
          },
          {
            label: this.$t('IDC.orgCustomer.description'),
            prop: 'productDesc',
            'sort-method': (a, b) => {
              return a.productDesc > b.productDesc ? 1 : -1;
            },
            align: 'left',
          },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: this.$t('operatorRoleCreate.productName'),
              key: 'productName',
              prop: 'productName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
            {
              label: this.$t('operatorRoleCreate.serviceName'),
              key: 'serviceName',
              prop: 'serviceName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      selectProductColums: [
        { label: this.$t('IDC.orgCustomer.productName'), prop: 'productName', align: 'left' },
        {
          label: this.$t('IDC.orgCustomer.EffectiveDate'),
          prop: 'effectiveDate',
          align: 'left',
          __slotName: 'effectiveDate',
        },
        {
          label: this.$t('IDC.orgCustomer.expirationDate'),
          prop: 'expireDate',
          align: 'left',
          __slotName: 'expireDate',
        },
        {
          label: this.$t('IDC.orgCustomer.chargeProfile'),
          prop: 'chargeProfileId',
          align: 'left',
          __slotName: 'chargeProfileId',
        },
        { label: this.$t('IDC.orgCustomer.description'), prop: 'productDesc', align: 'left' },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
        },
      ],
    };
  },
  methods: {
    handleUseDefault(data) {
      let obj = data.row;
      obj.ruleProfileId = data.row.defaultRuleProfileId;
    },
    checkGroupMsisdnOnBlur(rule, value, callback) {
      if (!value) {
        callback(new Error('Please enter the MSISDN'));
      } else {
        let params = {
          msisdn: value,
        };
        checkMsisdn(params).then((res) => {
          if (res.header && res.header.success) {
            if (res.check == 'NotExist') {
              return callback(new Error('The MSISDN is not existed.'));
            } else if (res.check == 'Invalid') {
              return callback(new Error('The MSISDN is invalid.'));
            } else {
              return callback();
            }
          } else {
            return callback(new Error('Query MSISDN error.'));
          }
        });
      }
    },
    chargeProfileUseDefault() {
      this.$emit('chargeUseDefault');
    },
    ruleProfileUseDefault() {
      this.$emit('ruleUseDefault');
    },
    productAddEvn() {
      this.productVisible = true;
    },
    loadProductParam(param) {
      return new Promise((resolve) => {
        let p = {
          page: param.pageNo || 1,
          productName: param.searchKey,
          selectProducts: this.dataObj.productInfoList.map((item) => item.productId),
          trustLevelId: this.dataObj.trustLevelId,
          segmentIds: this.dataObj.segmentIds,
        };
        queryAssignProduct(p)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.products.map((item) => {
                item.effectiveDateInit = item.effectiveDate;
                item.expireDateInit = item.expireDate;
                item.effectiveDate = dayjs(item.effectiveDate, 'DD/MM/YYYY').format('DD/MM/YYYY HH:mm:ss');
                item.expireDate = dayjs(item.expireDate, 'DD/MM/YYYY').format('DD/MM/YYYY HH:mm:ss');
                return { ...item };
              });
            }
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {});
      });
    },
    // Add Product 弹窗
    onProductChange(data) {
      data.forEach((item) => {
        let obj = Object.assign({}, item);
        obj.isProduct = 1;
        obj.chargeProfileId = obj.chargeProfileId || obj.defaultChargProfileId || '';
        obj.chargeProfiles = obj.chargeProfiles || '';
        this.dataObj.productInfoList.push(obj);
      });
      if (data.length > 0) {
        this.$emit('refresh-accountType', {});
      }
    },
    handleDelete(index) {
      this.$confirm('Are you sure to delete it?', '', {
        confirmButtonText: this.$t('common.button.delete'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        type: 'warning',
        customClass: 'delete-confirm',
      }).then(() => {
        this.dataObj.productInfoList.splice(index, 1);
        this.$emit('refresh-accountType', {});
      });
    },
    getEffectiveDateRange(expireDate) {
      return (rule, value, callback) => {
        if (value) {
          if (
            expireDate &&
            dayjs(value, 'DD/MM/YYYY HH:mm:ss').valueOf() > dayjs(expireDate, 'DD/MM/YYYY HH:mm:ss').valueOf()
          ) {
            callback(new Error('Effective Date cannot be later than Expire Date'));
          } else {
            callback();
          }
        } else {
          callback(new Error('Please select the correct date'));
        }
      };
    },
    getExpireDateRange(effectiveDate) {
      return (rule, value, callback) => {
        if (value) {
          if (
            effectiveDate &&
            dayjs(effectiveDate, 'DD/MM/YYYY HH:mm:ss').valueOf() > dayjs(value, 'DD/MM/YYYY HH:mm:ss').valueOf()
          ) {
            callback(new Error('Effective Date cannot be later than Expire Date'));
          } else {
            callback();
          }
        } else {
          callback(new Error('Please select the correct date'));
        }
      };
    },
    pickerOptionsEffectiveDate(data, data2 = '') {
      return {
        disabledDate: (time) => {
          let startDateVal = data;
          let endDateVal = data2;
          if (endDateVal) {
            return (
              time.getTime() < dayjs(startDateVal, 'DD/MM/YYYY').valueOf() ||
              time.getTime() > dayjs(endDateVal, 'DD/MM/YYYY').valueOf()
            );
          } else {
            return time.getTime() < dayjs(startDateVal, 'DD/MM/YYYY').valueOf();
          }
        },
      };
    },
    seletedChargeProfile(param) {
      this.dataObj.chargeProfileobj = {
        ...this.dataObj.selectChargeProfileList.filter((item) => item.chargeProfileId == param)[0],
      };
    },
    seletedRuleProfile(param) {
      this.dataObj.ruleProfileobj = {
        ...this.dataObj.selectRuleProfileList.filter((item) => item.ruleProfileId == param)[0],
      };
    },
  },
};
</script>
