<template>
  <div :data="dataObj" border style="width: 100%">
    <!-- Basic Info -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ ' Basic Product' }}</span>
      </div>
      <div class="group-card__body">
        <el-form-item label="Product" style="margin-bottom: 0px" required></el-form-item>
        <el-form-item :prop="`showproductInfoList`" required>
          <el-button
            v-if="dataObj.showproductInfoList.length > 0"
            @click="showProductPopup"
            plain
            size="small"
            type="primary"
          >
            <i class="el-icon-edit el-icon--right"></i>
            <span>
              {{ $t('IDC.orgCustomer.edit') }}
            </span>
          </el-button>

          <el-button v-else @click="showProductPopup" icon="el-icon-plus" plain size="small" type="primary">
            <span>
              {{ $t('common.button.add') }}
            </span>
          </el-button>
        </el-form-item>
        <el-form-item v-if="dataObj.showproductInfoList.length > 0">
          <table-page
            :columns="selectProductColums"
            :has-pagination="true"
            :outSideTableData="dataObj.showproductInfoList"
          >
            <template slot="trustLevel" slot-scope="scope">
              {{ scope.data.row.trustLevel }}:{{ scope.data.row.trustLevelName }}
            </template>
          </table-page>
        </el-form-item>
      </div>
    </div>
    <div class="segmentation"></div>
    <!-- Identity Info -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ ' Identity Info' }}</span>
      </div>
      <div class="group-card__body">
        <el-row class="detail" :gutter="20">
          <el-col v-bind="colOptions" class="identity">
            <el-form-item
              class="tip-wrapper"
              label="Group Code"
              required
              :rules="[{ validator: checkGroupCodeOnBlur, trigger: 'blur' }]"
              prop="identityInfo.groupCode"
            >
              <el-input v-model="dataObj.identityInfo.groupCode" placeholder="Please enter"></el-input>
            </el-form-item>
            <div class="identity-btn" @click="GeneratedCode">{{ 'Generate Another Identitier' }}</div>
          </el-col>
          <el-col v-bind="colOptions">
            <el-form-item class="tip-wrapper" label="Group Name" required :prop="`identityInfo.groupName`">
              <el-input v-model="dataObj.identityInfo.groupName" placeholder="Please enter"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          class="description"
          :label="$t('common.label.description')"
          prop="identityInfo.description"
          style="margin-bottom: 0"
        >
          <el-input
            :rows="4"
            style="margin-bottom: 25px"
            type="textarea"
            v-model="dataObj.identityInfo.description"
            placeholder="Please enter"
          ></el-input>
        </el-form-item>
      </div>
    </div>

    <div class="segmentation"></div>
    <!-- KYC Info -->
    <div class="group-card">
      <div class="group-card__header">
        <span class="group-card__title">{{ ' KYC Info' }}</span>
      </div>
      <div class="group-card__body">
        <kyc-form
          ref="groupKycInfo"
          identityType="1002"
          identityClassify="group"
          :kycInfo.sync="dataObj.kycFormData"
          mode="create"
          :basicProductId="dataObj.productInfoList.length ? dataObj.productInfoList[0].productId : ''"
        />
      </div>
    </div>
    <!-- product弹框 -->
    <portal-add-popup
      id="popup-property"
      :searchVisible.sync="searchVisible"
      :hasCheckBox="false"
      :isCheck="true"
      :isCheckMemory="false"
      :isSingleCheck="true"
      :hasPagination="true"
      :addForm="servicePopupConf"
      :childSearch="loadSegments"
      @submit-call-back="onPropertyChange"
      :init-checks="initCheckList"
    >
      <template slot="trustLevel" slot-scope="scope">
        {{ `${scope.row.trustLevel}:${scope.row.trustLevelName}` }}
      </template>
    </portal-add-popup>
  </div>
</template>
<script>
import TablePage from '@/components/tablePage/index.vue';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';
import PortalAddPopup from '@/components/portal-add-popup/index.vue';
import dayjs from 'dayjs';
import { queryProduct, queryGroupCode, checkGroupCode } from '../../../../api/api-group';
import { inputRequiredValidateObj, nonSpecialCharValidateObj } from '@/common/validator/common';

export default {
  name: 'GroupCreateInfo',
  components: { KycForm, PortalAddPopup, TablePage },
  props: {
    dataObj: {
      labels: Object,
      default() {
        return {};
      },
    },
  },
  provide() {
    return {};
  },
  data() {
    return {
      addProduct: 'Add Product',
      searchVisible: false,
      initCheckList: [],
      servicePopupConf: {
        title: 'Add Product',
        label: 'Product Name',
        idKey: 'productId',
        key: 'productId',
        columns: [
          { label: 'Product Name', prop: 'productName', align: 'left' },
          { label: 'Trust Level', prop: 'trustLevelName', align: 'left', __slotName: 'trustLevel' },
          { label: 'Description', prop: 'productDesc', align: 'left' },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: 'Product Name',
              key: 'productName',
              prop: 'productName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      selectProductColums: [
        { label: 'Product Name', prop: 'productName', align: 'left' },
        {
          label: 'Trust Level',
          prop: 'trustLevel',
          align: 'left',
          __slotName: 'trustLevel',
        },
        { label: 'Description', prop: 'productDesc', align: 'left' },
      ],
      inputRequiredValidateObj,
      nonSpecialCharValidateObj,
    };
  },
  created() {
    this.GeneratedCode();
  },
  methods: {
    showProductPopup() {
      this.searchVisible = true;
    },
    GeneratedCode() {
      const _this = this;
      queryGroupCode({ identityType: 'Group', isBtnTrigger: true }).then((res) => {
        if (res.header.success && res.identifier) {
          _this.dataObj.identityInfo.groupCode = res.identifier;
        }
      });
    },
    checkGroupCodeOnBlur(rule, value, callback) {
      if (!value) {
        callback(new Error('Please enter the code'));
      } else if (value.length > 12 || value.length < 6) {
        return callback(new Error('Must be between 6 and 12 characters'));
      } else {
        let params = {
          groupCode: value,
        };
        checkGroupCode(params).then((res) => {
          if (res.header && res.header.success) {
            if (res.check == 'NotExist') {
              return callback();
            } else if (res.check == 'Invalid') {
              return callback(new Error('The group code is invalid.'));
            } else {
              return callback(new Error('The group code is existed.'));
            }
          } else {
            return callback(new Error('Query group code error.'));
          }
        });
      }
    },
    onPropertyChange(data) {
      if (!data) return;
      this.initCheckList = [];
      this.initCheckList.push(data);
      this.$emit('App-Product', { content: data });
    },
    loadSegments(param) {
      return new Promise((resolve) => {
        let params = {
          page: param.pageNo || 1,
          productName: param.searchKey ? param.searchKey.productName : '',
        };
        queryProduct(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.products;
              res.total = res.page.totalSize;
            }
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {});
      });
    },
    pickerOptionsEffectiveDate(data) {
      return {
        disabledDate: (time) => {
          let endDateVal = data;
          if (endDateVal) {
            return time.getTime() < dayjs().startOf('date');
          } else {
            return time.getTime() < dayjs().startOf('date');
          }
        },
      };
    },
    pickerOptionsExpireDate(data, data1) {
      return {
        disabledDate: (time) => {
          let endDateVal = data;
          let beginDateVal = data1;
          if (endDateVal) {
            let stime = new Date(endDateVal).getTime() + 24 * 60 * 60 * 1000;
            let etime = new Date(beginDateVal).getTime() + 24 * 60 * 60 * 1000;
            return stime < etime;
          } else {
            return time.getTime() < dayjs().startOf('date');
          }
        },
      };
    },
    getEffectiveDateRange(rule, value, callback) {
      let values = value.split('/');
      let timeValues = values[2] + '/' + values[1] + '/' + values[0];
      let stime = new Date(timeValues).getTime();
      let time = dayjs().startOf('date');
      if (stime < time) {
        callback(new Error('Please select the correct date'));
      } else {
        callback();
      }
    },
    checkKYC() {
      this.$refs.groupKycInfo.validate((valid) => {
        this.$emit('groupkycinfo-validate', { content: valid });
      });
    },
    getGroupkycinfoDataEvn() {
      return this.$refs.groupKycInfo.getKYCRequestParams();
    },
  },
};
</script>
