<template>
  <div class="group-create-config">
    <div v-if="!createCompleted">
      <!-- header -->
      <portal-header :projectObj="titleConfig"></portal-header>
      <!-- stepTab -->
      <el-form
        class="add-form"
        label-width="100px"
        label-position="top"
        ref="ruleForm"
        :rules="queryFormRules"
        :model="groupModel"
      >
        <steps-tabs :activeName="activeName" :tabs-panel="tabsPanel">
          <template slot="first">
            <group-create-info
              ref="GroupInfo"
              :dataObj.sync="groupModel"
              @App-Product="setAppProduct"
              @groupkycinfo-validate="GroupKycInfoValidate"
            ></group-create-info>
          </template>
          <template slot="second">
            <group-create-details
              v-if="activeName == 'second'"
              :dataObj.sync="groupModel"
              :detaileLoading="detaileLoading"
              @refresh-accountType="refreshAccountType"
              @chargeUseDefault="chargeUseDefault"
              @ruleUseDefault="ruleUseDefault"
            ></group-create-details>
          </template>
          <template slot="third">
            <group-create-review :dataObj.sync="groupModel"></group-create-review>
          </template>
        </steps-tabs>
        <!-- Submit -->
        <footer-tap
          @reset="cancelEvn"
          @previous="previousEvn"
          @next="submitEvn"
          @submit="submitEvn"
          :submit-loading="footerButtonLoading"
          :active-name="activeName"
          :submitDisabled="!hasPerm('IC_IDENTITY_CREATEGROUP_BTN')"
          :steps="tabsPanel"
        />
      </el-form>
    </div>
    <portal-create-success
      v-else
      :successText="successText"
      detailText="View Detail >>"
      :addText="addjmuText"
      @view-detail="goViewDetail"
      @continue-add="goContinueToAdd"
    >
    </portal-create-success>
  </div>
</template>

<script>
import PortalCreateSuccess from '@/components/portal-create-success';
import StepsTabs from '@/components/stepsTabs';
import GroupCreateInfo from '../create/components/group-create-Info.vue';
import GroupCreateDetails from '../create/components/group-create-details.vue';
import GroupCreateReview from '../create/components/group-create-review.vue';
import footerTap from '@/components/portal-footer-tap';
import PortalHeader from '@/components/portal-header';
import { getDataFormRules } from '../../../validator/group.js';
import cookie from '@/common/utils/cookie';
import {
  checkInputKyc,
  querytrustLevelAndSegment,
  queryaddGroup,
  queryAccountType,
  queryGroupMemberRule,
} from '../../../api/api-group';
import dayjs from 'dayjs';
export default {
  components: {
    StepsTabs,
    GroupCreateInfo,
    PortalCreateSuccess,
    GroupCreateDetails,
    GroupCreateReview,
    footerTap,
    PortalHeader,
  },
  name: 'GroupcreateConfiguration',
  data() {
    return {
      identityId: '',
      createCompleted: false,
      submitLoading: false,
      successText: 'Created Successfully',
      addjmuText: 'Continue to Add >>',
      activeName: 'first',
      buttonText: 'Next',
      progressIndex: 0,
      tabsPanel: [
        {
          name: 'first',
          title: this.$t('IDC.orgCustomer.identityInfo'),
          subTitle: this.$t('IDC.orgCustomer.identityInfoSub'),
        },
        {
          name: 'second',
          title: 'Enter Details',
          subTitle: this.$t('IDC.orgCustomer.enterDetailsSub'),
        },
        {
          name: 'third',
          title: 'Review',
          subTitle: this.$t('IDC.orgCustomer.reviewSub'),
        },
      ],
      queryFormRules: getDataFormRules(),
      // 数据模型
      groupModel: {
        productInfoList: [],
        showproductInfoList: [],
        identityInfo: {
          groupCode: '',
          groupName: '',
          description: '',
        },
        trustLevel: '',
        segment: '',
        msisdn: '',
        pegisteredBy: cookie.get('user_account') || '',
        groupMemberRoleList: [
          // {
          //   groupRoleName: '',
          //   minimumNumberRequired: '',
          //   maximumNumberAllowed: '',
          //   assigntoFirstMemberbyDefault: '',
          // },
        ],
        profile: {
          chargeProfile: '',
          ruleProfile: '',
          ruleProfileList: [
            // {
            //   name: '',
            //   code: '',
            // },
            // {
            //   name: 'utjh',
            //   code: '2222',
            // },
          ],
          chargeProfileList: [
            // {
            //   name: 'fasdfasd',
            //   code: '11111',
            // },
            // {
            //   name: 'utjh',
            //   code: '2222',
            // },
          ],
        },
        accountList: [
          // {
          //   accountType: 'gghjj',
          //   currency: 'jjjjsfd',
          //   ruleProfile: '2222',
          // },
        ],
        kycFormData: {},
        chargeProfileobj: {},
        ruleProfileobj: {},

        selectChargeProfileList: [],
        selectRuleProfileList: [],
        selectSegmentName: '',
        trustLevelId: '',
        trustLevelName: '',
        accountTypeList: [],
        segmentIds: '',
      },
      product: {
        productName: '',
        EffectiveDate: '',
        ExpiryDate: '',
        chargeProfile: '',
        description: '',
      },
      groupMemberRole: {
        groupRoleName: '',
        minimumNumberRequired: '',
        maximumNumberAllowed: '',
        assigntoFirstMemberbyDefault: '',
      },
      account: {
        accountType: '',
        currency: '',
        ruleProfile: '',
      },
      detaileLoading: false,
      footerButtonLoading: false,
      defaultChargeProfileId: '',
      defaultRuleProfileId: '',
    };
  },
  computed: {
    titleConfig() {
      return {
        title: this.$t('IDC.group.createGroup'),
        desc: this.$t('IDC.group.dic'),
      };
    },
  },
  created() {},
  activated() {},
  methods: {
    cancelEvn() {
      this.$confirm('Are you sure to reset all data you have  entered/selected  in this page?', '', {
        confirmButtonText: 'Confirm',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {
        this.refreshCurrentPage();
      });
    },
    previousEvn() {
      this.changeProgressIndex(false);
    },
    submitEvn() {
      if (this.progressIndex == 2) {
        this.createGroup();
      } else if (this.progressIndex == 0) {
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
            this.$refs.GroupInfo.checkKYC();
          }
        });
        // this.GroupKycInfoValidate({ content: true });
      } else if (this.progressIndex == 1) {
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
            this.changeProgressIndex(true);
          }
        });
      }
    },
    changeProgressIndex(step) {
      if (step) {
        if (this.progressIndex < 2) {
          this.progressIndex++;
        }
      } else {
        if (this.progressIndex > 0) {
          this.progressIndex--;
        }
      }
      this.mapProgressIndex();
    },
    mapProgressIndex() {
      if (this.progressIndex == 0) {
        this.activeName = 'first';
        this.buttonText = 'Next';
      } else if (this.progressIndex == 1) {
        this.activeName = 'second';
        this.buttonText = 'Next';
      } else if (this.progressIndex == 2) {
        this.activeName = 'third';
        this.buttonText = 'Submit';
      }
    },
    setAppProduct(data) {
      let obj = null;
      let content = data.content;
      if (Array.isArray(content) && !content.length) {
      } else {
        obj = Object.assign({}, content);
        obj.isProduct = 0;
        obj.chargeProfileId = obj.chargeProfileId || obj.defaultChargProfileId || '';
        obj.chargeProfiles = obj.chargeProfiles || '';
      }

      if (this.groupModel.productInfoList.length == 0) {
        if (obj) {
          this.groupModel.productInfoList.push(obj);
          this.groupModel.showproductInfoList.push(obj);
        }
      } else {
        if (obj) {
          this.groupModel.showproductInfoList.splice(0, 1, obj);
          this.groupModel.productInfoList.splice(0, 1, obj);
        } else {
          this.groupModel.showproductInfoList.splice(0, 1);
          this.groupModel.productInfoList.splice(0, 1);
        }
      }
    },
    GroupKycInfoValidate(data) {
      if (data.content) {
        let param = {
          identityKYCData: this.$refs.GroupInfo.getGroupkycinfoDataEvn(),
          trustLevelId: this.groupModel.productInfoList[0].trustLevelId,
        };
        this.footerButtonLoading = true;
        checkInputKyc(param)
          .then((data) => {
            if (data.result) {
              this.loadQueryTrustLevelAndSegment();
              this.changeProgressIndex(true);
            }
          })
          .finally(() => {
            this.footerButtonLoading = false;
          });
      }
    },
    loadQueryTrustLevelAndSegment() {
      let obj = this.groupModel.productInfoList.find((item) => {
        return item.isProduct === 0;
      });
      let params = {
        selectBasicProductId: obj.productId,
        identityKYCData: this.$refs.GroupInfo.getGroupkycinfoDataEvn(),
      };
      this.detaileLoading = true;
      querytrustLevelAndSegment(params)
        .then((data) => {
          queryGroupMemberRule({ productIds: [obj.productId] }).then((res) => {
            if (res.header && res.header.success) {
              this.groupModel.groupMemberRoleList = res.groupTypeRoles;
            }
          });
          if (data.header && data.header.success) {
            this.groupModel.selectChargeProfileList = data.chargeProfileList || [];
            this.groupModel.chargeProfileobj = {
              ...(data.chargeProfileList || []).filter((item) => item.chargeProfileId == data.selectChargeProfileId)[0],
            };
            this.groupModel.selectRuleProfileList = data.ruleProfileList || [];
            this.groupModel.ruleProfileobj = {
              ...(data.ruleProfileList || []).filter((item) => item.ruleProfileId == data.selectedRuleProfileId)[0],
            };
            this.defaultChargeProfileId = data.defaultChargeProfileId;
            this.defaultRuleProfileId = data.defaultRuleProfileId;
            this.groupModel.selectSegmentName = data.selectSegmentName;
            this.groupModel.trustLevel = data.trustLevel;
            this.groupModel.trustLevelId = data.trustLevelId;
            this.groupModel.trustLevelName = data.trustLevelName;
            this.groupModel.accountTypeList = data.accountTypeRuleProfileBeanList.map((item) => {
              if (!item.ruleProfileId) {
                item.ruleProfileId = item.defaultRuleProfileId;
              }
              return { ...item };
            });
            this.groupModel.segmentIds = data.segmentIds || [];
            this.groupModel.productInfoList = [];
            data.products.forEach((item) => {
              this.groupModel.showproductInfoList.forEach((item1) => {
                let obj = Object.assign({}, item);
                obj.chargeProfileId = obj.chargeProfileId || obj.defaultChargProfileId || '';
                obj.chargeProfiles = obj.chargeProfiles || [];
                obj.effectiveDateInit = obj.effectiveDate;
                obj.expireDateInit = obj.expireDate;
                obj.effectiveDate = dayjs(obj.effectiveDate, 'DD/MM/YYYY').format('DD/MM/YYYY HH:mm:ss');
                obj.expireDate = dayjs(obj.expireDate, 'DD/MM/YYYY').format('DD/MM/YYYY HH:mm:ss');
                if (item.productId != item1.productId) {
                  obj.isProduct = 2;
                  this.groupModel.productInfoList.push(obj);
                } else {
                  obj.isProduct = 0;
                  this.groupModel.productInfoList.unshift(obj);
                }
              });
            });
          }
        })
        .finally(() => {
          this.detaileLoading = false;
        });
    },
    createGroup() {
      this.footerButtonLoading = true;
      queryaddGroup({
        trustLevelId: this.groupModel.trustLevelId,
        groupCode: this.groupModel.identityInfo.groupCode,
        groupName: this.groupModel.identityInfo.groupName,
        description: this.groupModel.identityInfo.description,
        trustLevel: this.groupModel.trustLevel,
        chargeProfileId: '' + this.groupModel.chargeProfileobj.chargeProfileId || '',
        ruleProfileId: '' + this.groupModel.ruleProfileobj.ruleProfileId || '',
        groupProductList: this.groupModel.productInfoList,
        segmentList: this.groupModel.segmentIds.map((item) => ({ segmentId: item })),
        groupKyc: this.$refs.GroupInfo.getGroupkycinfoDataEvn(),
        groupMember: {
          groupMemberMSISDN: this.groupModel.msisdn,
          groupMemberRoles: this.groupModel.groupMemberRoleList.map((item) => ({
            ...item,
            roleName: item.name,
            defaultRole: item.default,
          })),
        },
        basicKyc: {
          msisdn: this.groupModel.msisdn,
        },
        groupType: this.groupModel.productInfoList[0].productId,
        accountList: this.groupModel.accountTypeList.map((item) => ({
          accountType: `${item.accountType.accountTypeId || ''}`,
          currency: item.currency,
          ruleProfileId: `${item.ruleProfileId || ''}`,
        })),
      })
        .then((data) => {
          if (data.header && data.header.success) {
            this.changeProgressIndex(true);
            this.createCompleted = true;
            this.identityId = data.identityId;
          }
        })
        .finally(() => {
          this.footerButtonLoading = false;
        });
    },
    refreshAccountType() {
      if (this.groupModel.productInfoList.length > 0) {
        let productIds = [];
        this.groupModel.accountTypeList = [];
        this.groupModel.productInfoList.forEach((item) => {
          productIds.push(item.productId);
        });
        let params = {
          productIds: productIds,
          trustLevelId: this.groupModel.trustLevelId,
          segmentIds: this.groupModel.segmentIds,
        };
        queryAccountType(params).then((data) => {
          data.accountTypeRuleProfileBeanList.forEach((item) => {
            item.ruleid = item.ruleProfileId;
            item.rule = item.ruleProfileName;
            this.groupModel.accountTypeList.push(item);
          });
        });
      }
    },
    // ====successEvn======
    goViewDetail() {
      if (!this.identityId) {
        return;
      }
      let data = {
        identityId: this.identityId,
        groupCode: this.groupModel.identityInfo.groupCode,
      };
      this.$router.push({
        path: 'detail',
        query: { params: encodeURIComponent(JSON.stringify(data)) },
      });
    },
    goContinueToAdd() {
      this.refreshCurrentPage();
    },
    chargeUseDefault() {
      this.groupModel.chargeProfileobj = {
        ...this.groupModel.selectChargeProfileList.filter(
          (item) => item.chargeProfileId == this.defaultChargeProfileId
        )[0],
      };
    },
    ruleUseDefault() {
      this.groupModel.ruleProfileobj = {
        ...this.groupModel.selectRuleProfileList.filter((item) => item.ruleProfileId == this.defaultRuleProfileId)[0],
      };
    },
  },
};
</script>
