import {
  inputRequiredValidateObj,
  selectRequiredValidateObj,
  len6To12StrValidateObj,
  nonSpecialValidateObj,
  nonSpecialCharValidateObj,
} from '@/common/validator/common';
import { queryBusinessGroupList } from '@/views/identifier-center/api/api-group';

const checkGroupNameLengthValidateObj = {
  validator: (rule, value, callback) => {
    if (value.length > 64) {
      return callback(new Error('Please enter a maximum of 64 characters.'));
    }
    return callback();
  },
};

const checkGroupNameValidate = () => {
  const checkExist = (rule, value, callback) => {
    let param = {
      identitySortTypeDTO: {
        sortField: '',
        sortType: 'ASC',
      },
      groupName: value,
      pageNo: 1,
      pageSize: 2,
    };
    queryBusinessGroupList(param).then((res) => {
      if (res.header && res.header.success) {
        if (res.totalRecords > 0) {
          return callback(new Error('The Group Name is existed'));
        } else {
          return callback();
        }
      } else {
        return callback(new Error('Query Group Name failed.'));
      }
    });
  };
  return {
    validator: checkExist,
  };
};
const getDataFormRules = () => {
  return {
    'identityInfo.groupCode': [inputRequiredValidateObj, len6To12StrValidateObj, nonSpecialCharValidateObj],
    'identityInfo.groupName': [
      inputRequiredValidateObj,
      nonSpecialCharValidateObj,
      checkGroupNameLengthValidateObj,
      checkGroupNameValidate(),
    ],
    'identityInfo.description': [nonSpecialValidateObj],
    showproductInfoList: [selectRequiredValidateObj],
    msisdn: [inputRequiredValidateObj],
    'ruleProfileobj.ruleProfileId': [selectRequiredValidateObj],
  };
};
export { getDataFormRules };
